export default {
  name:"ChatSide",
  props:["list","count","targetid"],
  data(){
    return {

    }
  },
  methods:{
   changeRoom(id){
    this.$emit("changeroom",id);
   },
   closeRoom(id,event){
    if (event) {
      event.stopPropagation();
    }
    this.$emit("closeroom",id);
   }
  }
}



