import SRecorder from 'assets/js/SRecorder'
export default {
  name:"ControlBar",
  props:["socket","userId","userName","userAvatar","targetId"],
  data(){
    return {
      cmodel:'text',
      record:false,
      message:'',
      gRecorder:null,
      door:false,
      durationtime:0
    }
  },
  methods:{
    audioModelCtrol(evt){
      if(this.cmodel=='text'){
        this.cmodel='audio'
        var self = this;
        if(!self.gRecorder){
          SRecorder.get(function (rec) {
            self.gRecorder = rec;
            console.log(self.gRecorder);
          });
        }
      }else{
        this.cmodel='text'
      }
    },
    recordAudio(){
      this.record=true;
      if(!this.door) {
          this.gRecorder.start();
          this.durationtime = new Date().getTime();
          this.door = true;
      }
    },
    stopRecordAudio(){
      this.record=false;
      var stoptime = new Date().getTime();
      this.durationtime = Math.ceil((stoptime-this.durationtime)/1000)+'\"';
      if(this.door) {
          this.message=this.gRecorder.getBlob();
          this.gRecorder.clear();
          this.gRecorder.stop();
          this.door = false;
          this.sendMsg();
        }
    },
    sendMsg() {  
      var val = this.message; 
      var targetId = this.targetId;  
      if(typeof val =='string'&&$.trim(val)) {
        if(val.length >= 516) {
          val = val.slice(0, 500) + '...(输入太长，系统自动截断)';
        }
      }

      var data = {
          id: this.userId,
          msg: val,
          name: this.userName,
          avatar: this.userAvatar,
          targetId: targetId,
          mtype:"log",
          stime:null,
          durationtime:this.durationtime
        };
        if(!this.checkRobot()) return;
        this.socket.emit(targetId == 'group' ? 'gm' : 'pm', data);
        this.$emit('addchatlog',data, targetId, true);
        this.message=""
    },
    sendButnClick (evt) {
      this.sendMsg();
    },
    checkRobot(){
     var i = 0;
      while(i++ < 1E3) {
        clearInterval(i);
      }
      if(document.visibilityState && document.visibilityState !== 'visible') {
          return false;
      }
      return true;
  },
  }
}



