import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import ChatRoom from '../components/ChatRoom'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/chatroom',
      name: 'ChatRoom',
      component: ChatRoom
    }
  ]
})



