export default {
  name:"ChatPanels",
  props:["chatPanels","targetId","myname","infocount"],
  data:function(){
    return {
      tchatpanels:this.chatPanels,
      timeint:null,
      volumeclass:'fa-volume-up'
    }
  },
  methods:{
    privateChat:function(item){
      this.$emit("privatechat",item);
    },
    playAudio:function(e){
      var audioEle = e.target.querySelector('audio');
      if(audioEle){
        audioEle.play();
      }
    },
    receive(d) {
      return window.URL.createObjectURL(new Blob([d],{type:"audio/wav"}));
    },
    scroll:function(){
      var self = this;
      setTimeout(function(){
        var $target = $("#" + self.targetId);
        var $box = $('.chatroom-pannel-bd');
        var H = $target.height();
        var DELTA = $box.height();        
        if(H > DELTA) {
          $box.scrollTop(H - DELTA);
        }
      },100);
      
    }
  },
  watch:{
    'chatPanels':{
      handler:function(nval){
        if(nval[0].id==this.targetId){
          this.scroll();
        }
      },
      deep:true
    },
    'targetId':function(){
      this.scroll();
    }
  }
}



